<template>
	<fragment>
		<Gallery
			:images="gallery.images"
			:activeIndex="gallery.index"
			@index-changed="changeIndex"
			@exit="closeGallery"
		/>
		<div class="view" :style="{animation: !$loaded ? 'bottomElation 0.5s 1s both' : 'bottomElation 0.5s both'}">
			<section>
				<OrderHistory
					title="Budujemy"
					content="
						Jesienią 2018 zgłosił się do nas deweloper z prośbą o budowę szeregowca w Rumii.
						Prace trwały pół roku, a mieszkania szybko znalazły swoich właścicieli.
						Wszystko dzięki naszej <span class='naj'><strong>Na-J</strong>wybitniejszej</span> pracy.
					"
					id="terracedHouse"
					:images="photos.terracedHouse"
					:primaryIndex="4"
					@photo-selected="selectPhoto"
				/>
				<OrderHistory
					title="Lubimy wyzwania"
					content="
						We wrześniu 2020 roku podjęliśmy się budowy kanału dla stacji diagnostycznej.
						Jak wiadomo, nie należy to do łatwych czynności, natomiast nasza firma nie boi sie nawet <span class='naj'><strong>Na-J</strong>trudniejszych</span> wyzwań!
					"
					id="carStation"
					:images="photos.carStation"
					:primaryIndex="0"
					@photo-selected="selectPhoto"
				/>
				<OrderHistory
					title="Docieplamy"
					content="
						Latem 2018 roku podjęliśmy się ocieplenia jednorodzinnego domku w urokliwym miejscu przy lesie.
						Klientka była bardzo zadowolona, bo jak się okazało rachunki za ogrzewanie gazowe miała niższe niż początkowo szacowała.
						Ta historia kolejny raz potwierdziła <span class='naj'><strong>Na-J</strong>wyższą</span> jakość naszych usług.
					"
					id="warming"
					:images="photos.warming"
					:primaryIndex="7"
					@photo-selected="selectPhoto"
				/>
				<OrderHistory
					title="Spełniamy Marzenia"
					content="
						Pomysł zrodził się w czerwcu 2020 roku na spotkaniu towarzyskim przy grillu.
						Wykonanie zajęło nam jedynie 2 tygodnie.
						Spełniliśmy w ten sposób kolejne z <span class='naj'><strong>Na-J</strong>skrytszych</span> marzeń naszych klientów.
					"
					id="swimmingPool"
					:images="photos.swimmingPool"
					:primaryIndex="6"
					@photo-selected="selectPhoto"
				/>
			</section>
			<section>
				<h2 class="orders-title">Pozostałe realizacje</h2>
				<div class="orders">
					<Order
						title="Budowa dla dewelopera"
						id="buildingAHouse"
						:images="photos.buildingAHouse"
						:primaryIndex="4"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Wiatrołapy w Gdyni"
						id="vestibules"
						:images="photos.vestibules"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Remont w kamienicy"
						id="renovation"
						:images="photos.renovation"
						:primaryIndex="4"
						@photo-selected="selectPhoto"
					/>
				</div>
			</section>
		</div>
	</fragment>
</template>

<script>
	import OrderHistory from '@/Components/Realizations/OrderHistory'
	import Order from '@/Components/Realizations/Order'
	import Gallery from '@/Components/Realizations/Gallery'
	const importAll = (r) => {
		const images = []
		r.keys().map(r).forEach((img, i) => images.push({
			image: img,
			id: i
		}))
		return images;
	}
	const terracedHouse = importAll(require.context('@/Assets/Images/realizations/terraced-house', false, /\.(png|jpe?g|svg)$/));
	const carStation = importAll(require.context('@/Assets/Images/realizations/car-station', false, /\.(png|jpe?g|svg)$/));
	const warming = importAll(require.context('@/Assets/Images/realizations/warming', false, /\.(png|jpe?g|svg)$/));
	const swimmingPool = importAll(require.context('@/Assets/Images/realizations/swimming-pool', false, /\.(png|jpe?g|svg)$/));

	const buildingAHouse = importAll(require.context('@/Assets/Images/realizations/building-a-house', false, /\.(png|jpe?g|svg)$/));
	const renovation = importAll(require.context('@/Assets/Images/realizations/renovation', false, /\.(png|jpe?g|svg)$/));
	const vestibules = importAll(require.context('@/Assets/Images/realizations/vestibules', false, /\.(png|jpe?g|svg)$/));

	export default {
		name: "Realizations",
		components: {
			OrderHistory,
			Order,
			Gallery
		},
		data() {
			return {
				photos: {
					terracedHouse,
					carStation,
					swimmingPool,
					warming,

					buildingAHouse,
					renovation,
					vestibules,
				},
				gallery: {}
			}
		},
		methods: {
			changeIndex(number) {
				this.gallery.index += number
			},
			closeGallery() {
				this.gallery = {}
			},
			selectPhoto(id, index) {
				this.gallery = {
					images: this.photos[id],
					index
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.orders-title {
		position: relative;
		display: inline-block;
		margin: 100px 0 30px 0 ;
		font-size: 35px;
		@include line;
	}
	.orders {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}
</style>